.App {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  flex:1;
  width:100vw;
  min-height: calc(100vh - 4rem);
  padding:1rem 0rem;
  flex-direction: column;
}
.menu{
  position: fixed;
  bottom: 0px;
  display: flex;
  height: 3rem;
  background-color: white;
  width:100%;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.menu div{
  flex:1;
  text-align: center;
  font-weight: bold;
  cursor: pointer;
  padding:1rem;
}
.menu div:hover{
  background-color: #AAA;
}

.pageHide{
  display: none;
}

.About{
  text-align: center;
  color:#fff;
}

.About img{
  max-width: 280px;
}

.activeMenu{
  background-color: #DDD;
}
