.card_markdown td, .card_markdown th {
    border-bottom: 1px solid #333;
    padding: 0.5rem;
    font-size: 0.8rem;
}
.dashboard{
  padding:1rem;
  width:100vw;
  background-color: #fff;
}

.dashboard h1, .dashboard h2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 2px 3px #333;
  padding:0.5rem;
  font-size: 1.2rem;
}

.dashboard ul{
  padding:0px;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin:0px;
}

.dashboard ul li {
  width:100%;
  margin:0.5rem 0;
  padding:1rem;
  box-shadow: 1px 3px 4px #333;
  text-transform:capitalize;
  text-align: center;
  cursor:pointer;
}
.card{
  padding-bottom: 0rem !important;
}
.currentCard{
  padding:1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  width:100%;
  
}

.card_markdown table{
  width:100%;
}
.boardTitle{
   display: block;
  text-align: left;
  padding-left: 1rem;
  width: 340px;
  word-break: break-all;
}
h2 .boardTitle{
  margin-left: 2.5rem;
}

.cardHeader{
  display: block;
  width: calc(100% + 2rem);
  padding:2rem 1rem;
  margin-left: -1rem;
  margin-top: -1rem;
  border-bottom:1px solid #333;
  background-color: #AAA;
  font-weight: bold;
  box-shadow: 0px 2px 3px #333;
}
.bckBoards{
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width:48px;
  height: 48px;
  background-color: #FFF;
  color:#333;
  box-shadow: 1px 1px 3px #333;
  border-radius: 50%;
  font-size: 32px;
  vertical-align: middle;
  padding-bottom: 0.4rem;
  padding-right:0.1rem;
}
.PAT{
  margin-left: -1rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}
.PAT label{
  font-weight: bold;
}
.PAT input{
  padding:0.5rem 1rem;
  margin:0.5rem 0.75rem;
  min-width: calc(100% - 1rem);
   font-size: 1rem;
}

.PAT button{
  padding:0.5rem 1rem;
  font-weight: bold;
  font-size: 1rem;
}
.PAT a {
  margin-top:1rem;
  color:#333;
  text-decoration: none;
  border-radius: 27px;
  border: 1px solid #333;
  padding:0.5rem 1rem;
}

@media (min-width:768px) {
  .dashboard ul li {
    width:180px;
  }
}

@media (min-width:1024px) {
  .dashboard {
    display: flex;
    max-width: 1200px;
    margin: 0px auto;
  }
  .dashboard .filter{
    height: calc(100vh - 5.2rem);
    overflow-y: scroll;
    width:380px;
    box-shadow: 3px 0px 4px #333;
    margin-top:-1rem;
    margin-bottom:-1rem;
    margin-left:-1rem;
  }
  .dashboard ul li {
    width: 90%;
  }
  .currentCard{
    width:80%;
    max-height: calc(100vh - 5.2rem);
    overflow-y: scroll;
  }
}
