.CustomTimer{
  background-color: #fff;
  padding:1rem;
  width:100vw;
}

.CustomTimer .ToolBar div {
  padding:0.5rem 1rem;

}

.CustomTimer .ToolBar div label{
  display: block;
  font-weight: bold;
  width:100%;
  margin-bottom: 0.5rem;
}

.CustomTimer .ToolBar div input{
  font-size: 1rem;
  padding: 0.5rem 1rem;
  width:100%;
}

.CustomTimer .ToolBar div button{
  display: block;
  background-color: #24a324;
  font-weight: bold;
  width:100%;
  padding:1rem;
  border:#333 solid 1px;
  box-shadow: 1px 1px 5px #000;
}

.TimersBoard{
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  padding:0rem 1rem;
}
.TimerItem{
  box-shadow: 1px 1px 3px #333;
  padding:1rem 0.5rem;
  margin-top:1rem;
}

@media (min-width:768px) {
  
}

@media (min-width:1024px) {
  .CustomTimer{
    display: flex;
    max-width: 1200px;
    margin: 0px auto;
  }
  .CustomTimer .ToolBar{
    min-height: calc(100vh - 5.2rem);
    overflow-y: scroll;
    width:380px;
    box-shadow: 3px 0px 4px #333;
    margin-top:-1rem;
    margin-bottom:-1rem;
    margin-left:-1rem;
    padding-top:1rem;
  }
  .TimersBoard{
     width:100%;
     max-height: calc(100vh - 5.2rem);
     overflow-y: scroll;
     justify-content: flex-start;
  }
  .TimerItem{
    margin:1rem 0rem 0rem 1rem;
  }
}
